import React from "react"
import ModuleDevSimulator from "../.."
import { HUtils, MyLib } from "@macashipo/mlib"
import { Input, Button, Label } from "reactstrap"
import { MyModal } from "src/core/components"
import styled from "styled-components"

const Container = styled.div`
  display: flex;
  flex-direction: column;
  & .title {
    margin-bottom: 0.5rem;
  }
  & .a-box {
    & .react-codemirror2 {
      border: 1px solid #dddddd;
    }
  }
`
const InputGroup = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  font-size: 14px;
  margin-bottom: 0.5rem;
  & .check-box-wrapper {
    background-color: rgb(255 255 255 / 80%);
    border: 1px solid #ced4da;
    border-radius: 0.25rem 0 0 0.25rem;
    border-right: 0;
    padding: 0.375rem 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    & input[type="checkbox"] {
      position: relative;
      margin: 0px;
    }
  }
  & .form-control {
    font-size: 14px;
    border-radius: 0 0.25rem 0.25rem 0;
  }
`
const FooterContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  & > button {
    width: fit-content;
    margin-top: 1rem;
    font-size: 14px;
  }
`

const MUI_DevSimulator = () => {
  const initialState = {
    currentBaseUrl: ModuleDevSimulator.getLocalBaseUrl(),
    currentConfigJson: ModuleDevSimulator.getLocalConfig() || "",
    isDebug: ModuleDevSimulator.getIsUsingLocalBaseUrl(),
  }
  const [currentBaseUrl, setCurrentBaseUrl] = React.useState(
    initialState.currentBaseUrl
  )
  const [currentConfigJson, setCurrentConfigJson] = React.useState(
    initialState.currentConfigJson
  )
  const [validConfigJson, setValidConfigJson] = React.useState(true)
  const [isDebug, setIsDebug] = React.useState(initialState.isDebug)
  const refCode = React.useRef(null)
  const onChangeBaseUrl = value => {
    setCurrentBaseUrl(value)
  }
  const onChangeIsDebug = () => {
    setIsDebug(!isDebug)
  }
  const onSave = () => {
    ModuleDevSimulator.setLocalBaseUrl(currentBaseUrl)
    ModuleDevSimulator.setIsUsingLocalBaseUrl(isDebug)
    try {
      let _editor = HUtils.get(refCode, "current.editor")
      if (_editor && _editor.getValue) {
        let _currentConfigJson = _editor.getValue()
        ModuleDevSimulator.setLocalConfig(_currentConfigJson)
        let _objConfig = JSON.parse(_currentConfigJson)
      }
    } catch (error) {
      console.error("Parse Json error")
    }
    MyModal.hideModal("myui")
    window.location.reload()
  }
  return (
    <Container>
      <Label className="title">Base Url</Label>
      <InputGroup>
        <div className="check-box-wrapper">
          <Input
            type="checkbox"
            checked={isDebug}
            onChange={() => onChangeIsDebug()}
          />
        </div>
        <Input
          type="text"
          value={currentBaseUrl}
          placeholder="Base Url"
          onChange={e => onChangeBaseUrl(e.currentTarget.value)}
        />
      </InputGroup>
      <div>
        Config{" "}
        {validConfigJson === false && (
          <span style={{ color: "red" }}>
            <i className="fa fa-exclamation-triangle" />
          </span>
        )}
      </div>
      <div className="a-box">
        <MyLib
          type={"code_mirror"}
          innerRef={r => {
            console.warn("innerRef")
            refCode.current = r
          }}
          value={currentConfigJson}
          onChange={(editor, metadata, value) => {
            console.warn("onChange", editor, metadata, value)
            let _isJson = true
            if (value) {
              try {
                JSON.parse(value)
              } catch (e) {
                _isJson = false
              }
            }
            if (validConfigJson != _isJson) {
              setValidConfigJson(_isJson)
            }
          }}
          options={{
            mode: {
              name: "javascript",
              json: true,
              statementIndent: 2,
            },
            lineNumbers: true,
            lineWrapping: true,
            indentWithTabs: false,
            tabSize: 2,
            theme: "default",
          }}
        />
      </div>
      <FooterContainer>
        <Button color="primary" onClick={() => onSave()}>
          Save
        </Button>
      </FooterContainer>
    </Container>
  )
}
export default MUI_DevSimulator
